@font-face {
    font-family: 'gilroy-regular';
    font-weight: 400;
    font-style: normal;
    src: local('gilroy-regular'),
        url('../fonts/gilroy-regular.woff2') format('woff2'),
        url('../fonts/gilroy-regular.woff') format('woff');
    font-display: swap;
}

